import React from "react";
import {Card, Badge} from "antd";
import {PROJECT_OVERVIEW_DATA} from "../api";
import axios from "axios";

class OGrid extends React.Component {
  constructor(props){
    super(props);
    this.state={
      projectId: props.projectId,
      projectCompleteness: "",
      userProgress: "",
      userClicksSaved: "",
      workHoursSaved: "",
      lastTrainingDate: "",
      modelAccuracy: "",
      deployStatus: "",
      apiStatus: ""
    }

    this.getProjectOverview(this);
  }

  getProjectOverview(self){
    axios.get(PROJECT_OVERVIEW_DATA+"?projectId="+self.state.projectId, {
    data: "data"
    })
    .then(function (response) {
      console.log("MyData");
      console.log(response);
      if(response.status===200){
        self.setState({
          projectCompleteness: response.data["project_completeness"],
          userProgress: response.data["user_progress"],
          userClicksSaved: response.data["user_clicks"],
          workHoursSaved: response.data["working_hours_saved"],
          lastTrainingDate: response.data["training_date"],
          modelAccuracy: response.data["model_accuracy"],
          deployStatus: response.data["deployment_status"],
          apiStatus: response.data["api_status"]
        })
      }
    })
    .catch(function (error) {
      console.log(error);
    });
  }

  render(){
  return (
    <Card title="Project Overview">
      <Card.Grid>Project Completeness <br/><Badge key={"Completeness"} count={this.state.projectCompleteness} style={{backgroundColor: "#1D8FDE"}}/></Card.Grid>
      <Card.Grid>User's Progress <br/> <Badge key={"UserP"} count={this.state.userProgress} style={{backgroundColor: "#1D8FDE"}}/></Card.Grid>
      <Card.Grid>User Clicks Saved <br/><Badge key={"UserC"} count={this.state.userClicksSaved} style={{backgroundColor: "#1D8FDE"}}/></Card.Grid>
      <Card.Grid>Estimated Working Hours Saved <br/><Badge key={"WH"} count={this.state.workHoursSaved} style={{backgroundColor: "#1D8FDE"}}/></Card.Grid>
      <Card.Grid>Last Training Instance Date <br/><Badge key={"TID"} count={this.state.lastTrainingDate} style={{backgroundColor: "#1D8FDE"}}/></Card.Grid>
      <Card.Grid>Overall Model Accuracy <br/><Badge key={"OMA"} count={this.state.modelAccuracy} style={{backgroundColor: "#1D8FDE"}}/></Card.Grid>
      <Card.Grid>Deployment Status <br/><Badge key={"DS"} count={this.state.deployStatus} style={{backgroundColor: "#1D8FDE"}}/></Card.Grid>
      <Card.Grid>API Status <br/><Badge key={"AS"} count={this.state.apiStatus} style={{backgroundColor: "#1D8FDE"}}/></Card.Grid>
    </Card>
  );
}
};

export default OGrid;
